




























import { Component, Vue } from 'vue-property-decorator';

@Component
export default class App extends Vue {
  private activeIndex = 'Home';

  private keyword = '';

  private handleSelect(key: string) {
    this.$router.push({
      name: key,
    });
  }

  private onSearch() {
    window.open(`https://www.baidu.com/s?word=${this.keyword}`);
  }
}
